import {Toolbar,AppBar,Stack,Divider,Grid,MenuItem} from '@mui/material'
import {Link} from 'react-router-dom'

export default function NavBar(){
    return (
            <AppBar position="fixed" style={{background: '#fff'}}>
                <Toolbar>
                    <Grid container 
                        spacing={0}
                        direction="column"
                        alignItems="center"
                        justifyContent="center">
                        <Grid item xs={12} sm={6} md={4}>
                            
                            <Stack
                                direction="row"
                                divider={<Divider orientation="vertical" flexItem />}
                                spacing={2}
                                >
                                <Link to="/categorias" target="_self" rel="noopener noreferrer">
                                    <MenuItem>
                                    <svg aria-label="Home" class="_8-yf5 " color="#262626" fill="#262626" height="24" role="img" viewBox="0 0 24 24" width="24"><path d="M9.005 16.545a2.997 2.997 0 012.997-2.997h0A2.997 2.997 0 0115 16.545V22h7V11.543L12 2 2 11.543V22h7.005z" fill="none" stroke="currentColor" stroke-linejoin="round" stroke-width="2"></path></svg>
                                    </MenuItem>                                    
                                </Link>
                                <Link to="/categorias/todos" target="_self" rel="noopener noreferrer">
                                    <MenuItem>
                                    <svg aria-label="Find People" class="_8-yf5 " color="#262626" fill="#262626" height="24" role="img" viewBox="0 0 24 24" width="24"><polygon fill="none" points="13.941 13.953 7.581 16.424 10.06 10.056 16.42 7.585 13.941 13.953" stroke="currentColor" stroke-linecap="round" stroke-linejoin="round" stroke-width="2"></polygon><polygon fill-rule="evenodd" points="10.06 10.056 13.949 13.945 7.581 16.424 10.06 10.056"></polygon><circle cx="12.001" cy="12.005" fill="none" r="10.5" stroke="currentColor" stroke-linecap="round" stroke-linejoin="round" stroke-width="2"></circle></svg>
                                    </MenuItem>   
                                </Link>
                                
                                <Link to="/buscas" target="_self" rel="noopener noreferrer">
                                    <MenuItem>
                                    <svg aria-label="Find People" class="_8-yf5 " color="#262626" fill="#262626" height="24" role="img" viewBox="0 0 24 24" width="24"><path d="M13.173 13.164l1.491-3.829-3.83 1.49zM12.001.5a11.5 11.5 0 1011.5 11.5A11.513 11.513 0 0012.001.5zm5.35 7.443l-2.478 6.369a1 1 0 01-.57.569l-6.36 2.47a1 1 0 01-1.294-1.294l2.48-6.369a1 1 0 01.57-.569l6.359-2.47a1 1 0 011.294 1.294z"></path></svg>
                                    </MenuItem>   
                                </Link>
                                
                                
                                {/* <Link to="/time" target="_self" rel="noopener noreferrer">
                                    <MenuItem><Typography>
                                        Time
                                    </Typography></MenuItem>   
                                </Link> */}
                                    
                            </Stack>
                        </Grid>
                    </Grid>
                
                    
                </Toolbar>   
            </AppBar>
        )
}