import React from 'react';
import ReactDOM from 'react-dom/client';
import { Routes,Route, HashRouter } from "react-router-dom";

import App from './App';
import Page from './component/page'
import PageId from './component/router/pageId'

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <HashRouter>
    {/* <BrowserRouter */}
  {/*https://github.com/remix-run/react-router/blob/main/docs/getting-started/tutorial.md 
  https://reactrouter.com/docs/en/v6/getting-started/concepts#history-and-locations
  */}
    <Routes>
      <Route path="/" element={<App />} />
      <Route path="/categorias" element={<Page page={"categorias"}/>}/> 
      <Route path="/categorias/todos" element={<Page page={"todos"}/>}/>   
      <Route path="/categorias/:pageId" element={<PageId/>}></Route>

      <Route path="/inventos" element={<App />} />
      <Route path="/inventos/categorias" element={<Page page={"categorias"}/>}/> 
      <Route path="/inventos/categorias/todos" element={<Page page={"todos"}/>}/>    
      <Route path="/inventos/categorias/:pageId" element={<PageId/>}></Route>
      <Route path="/inventos/categorias/todos" element={<Page page={"todos"}/>}/> 

      
      {/* <Route path="/inventos/time" element={<Time/>}/> 
      <Route path="/inventos/buscas" element={<Buscas/>}/>  */}
      {/* <Route path="*" element={<App />} /> */}
    </Routes>
  </HashRouter>
);
